@import "mixin";


.popup:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: $violet;
	opacity: 0.8;
	border-radius: 10px;
	// z-index: 1
	@include box-shadow(inset 0px -0px 100px 70px rgba(0,0,0,0.8)); }


.popup {

	max-width: 800px;
	width: 60%;
	min-width: 150px;

	max-height: 500px;
	height: 50%;
	min-height: 200px; }

.popup {
	display: none;
	left: 50%;
	position: fixed;
	top: 50%;
	z-index: 3000;
	border-radius: 10px;

	// background: $violet
	@include transform(translate(-50%, -50%));
	// @include flex
	justify-content: space-around;
	.popup-img {
		height: 70%;
		width: 40%;
		// background: url('/img/img-dj.png') no-repeat 50% 50%
		position: relative;
		background-size: cover;
		border-radius: 5px;
		@include transition-ease(0.4s); }



	.popup-text {
		width: 40%;
		height: 70%;
		position: relative;
		overflow: hidden;
		color: $white;
		@include transition-ease(0.4s);
		font-size: 22px;
		font-family: FuturaPTL;
		font-weight: 700;
		letter-spacing: 1px;
		overflow: auto; }

	.popup-h {
		margin-bottom: 15px;
		font-size: 30px; }
	.open {
		text-decoration: none;
		max-width: {}
		width: 15%;
		min-width: {}

		max-height: {}
		height: 10%;
		min-height: {}

		position: absolute;
		bottom: 3%;
		right: 3%;
		@include transition-ease(0.4s);
		color: $white;
		text-align: center;
		background: darken($oceanblue, 30%);
		border: 1px solid $white;
		border-radius: 5px;
		div {
			position: relative;
			font-size: 24px; } }
	.open:hover {
		background: $oceanblue; } }
// .popup-more
// 	@include flex

.popup-gallery:before {
	opacity: 0; }
.popup-gallery {
	overflow: auto;
	height: 70%;
	max-height: 900px;

	.gallery-item:before {

		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $black;
		opacity: 0;
		border-radius: 10px;
		@include transition-ease(0.4s); }

	.gallery-item {
		width: 100%;
		height: 50%;
		border-radius: 10px;
		background-size: 100%;
		position: relative;
		margin-bottom: 10px; }

	.gallery-item:last-child {
		margin-bottom: 0; }

	.gallery-item:hover:before {
		opacity: 0.3; } }





.popup-about:before {
	opacity: 0;
	// height: 100%
	// background: $black
 }	// overflow: hidden

.popup-about {
	// @include box-shadow(inset 0px -0px 100px 70px rgba(0,0,0,0.8))
	// width: 70%
	padding: 15px;
	overflow: auto;
	display: none;
	// display: block
	// background: $darkviolet
	background: $white;
	// width: 500px
	height: 70%;
	// background: #fff
 /// position: relative
 /// z-index: 3110

	img {
		float: right;
		// clear: right
		// display: inline-block
		border-radius: 10px;
		position: relative;
		z-index: 15;
		margin: 0 1.5% 5px 1.5%;
